.wrap{
	width: 100%;
	height: 600px;
	display: grid;
	grid-template-columns: 3fr 9fr;
	grid-gap: 3%;
	background-color: darkgray;
}
.block{
	margin: 7%;
	background-color: rgb(88, 197, 152);
	
}
.blockDialogs, .blockMessage{
	height: 570px;
}

.blockDialogs{
	overflow-y: auto;
	background-color: darkseagreen;
}
.blockMessage{
	padding: 2%;
}
.messageWrap{
	height: 450px;
	overflow-y: auto;

}
.oneMessage{
	background-color: rgba(43, 42, 49, 0.5);
	margin: 2%;
	padding: 2%;
	
}
.bodyT{
	color: black;
	font-size: medium;
	font-family:sans-serif;
	max-width: 400px;
	height: auto;
	word-wrap: break-word;
}
.noMessage{
	padding:30%;
	font-size: large;
}
.formSend{
	padding: 2%;
}